const events = { close: 'close' };

export default {
    props: {
        modalVisible: Boolean,
    },
    data: function() {
        return {
            accepted: Boolean,
        };
    },
    methods: {
        close: function() {
            this.$emit(this.events.close, { accepted: this.accepted });
        },
    },
    computed: {
        visible: {
            get: function() {
                return this.modalVisible;
            },
            set: function() {
                this.close();
            },
        },
    },
};
